import React from 'react';
import {Link} from 'react-router-dom';

const li = [
    {url: '/genres', title: ' '},
    {url: '/genre/RAP', title: ''},
    {url: '/genre/HIP_HOP', title: '-'},
    {url: '/genre/POP', title: ''},
    {url: '/genre/POP_RAP', title: '-'},
]

const HeaderGenres = (props) => (
    <div className="mt16">
        <span className="fw400 fs14"></span>
        <ul>
            {li.map((li, index) => (
                <li key={index}>
                    <Link to={li.url} onClick={props.click}>
                        {li.title}
                    </Link>
                </li>
            ))}
        </ul>
    </div>
)

export default HeaderGenres;